import React, {useEffect, useState} from 'react';
import { CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';

import './doctor.scss';
import { Link } from 'react-router-dom';
import CIcon from '@coreui/icons-react';
import { cilTrash } from '@coreui/icons';


const DoctorList=()=> {
  const [doctor, setDoctor] = useState([])

  useEffect(() => {
    fetch(process.env.REACT_APP_API_URL+"doctor/list", {
        method: 'POST', // or 'PUT'
        headers: {
            'Content-Type': 'application/json',
        }
    }).then(e=>e.json()).then((data)=>{
        setDoctor(data.data);
    })
  }, [])

  const deletePatient = (arg)=>{
    window.confirm("Are you Sure you want to delete this");
  }
  

  return (
    <>
     <div className='d-flex justify-content-start align-items-center mb-3'>
        <h2 className='mb-3'>Doctor List</h2>
    </div>
    
    <div className='arTableWrapper'>
        <CTable>
            <CTableHead>
                <CTableRow>
                <CTableHeaderCell width={220} scope="col">UID</CTableHeaderCell>
                <CTableHeaderCell scope="col">Full Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Mobile No</CTableHeaderCell>
                <CTableHeaderCell scope="col">Degree</CTableHeaderCell>
                <CTableHeaderCell width={240} scope="col">Specialization</CTableHeaderCell>
                <CTableHeaderCell width={60} scope="col">Action</CTableHeaderCell>
                </CTableRow>
            </CTableHead>
            <CTableBody>
                {doctor.map((i, k)=>(
                    <CTableRow key={k}>
                        <CTableDataCell scope="row"><Link to={"/doctors/"+i._id}> {i.uid}</Link></CTableDataCell>
                        <CTableDataCell>Dr. {i.first_name}{' '}{i.last_name}</CTableDataCell>
                        <CTableDataCell>{i.mobile}</CTableDataCell>
                        <CTableDataCell>{i.doctor_degree}</CTableDataCell>
                        <CTableDataCell>{i.doctor_specialization}</CTableDataCell> 
                        <CTableDataCell className='text-center'> <div onClick={()=>{deletePatient(i._id)}} className='deleteBtnAr'><CIcon icon={cilTrash} /></div></CTableDataCell>                         
                    </CTableRow>
                ))}                
            </CTableBody>
        </CTable>
    </div>
    </>)
}
export default DoctorList;